<template>
  <div class="SalesAnalysis">
    <el-button-group class="buttonStyle">
      <el-button size="medium" v-for="item in itemList" :key="item.index" :type="item.type" @click="clickButton(item)">{{ item.name }}</el-button>
    </el-button-group>
    <ve-line :data="lineChartData" height="40vh" :style="{ width: '100%'}"></ve-line>
    <ve-histogram :data="lineChartData" height="40vh" :style="{ display: 'inline-block',  width: '48%'}"></ve-histogram>
    <ve-pie :data="lineChartData" height="40vh" :style="{ display: 'inline-block', width: '48%'}"></ve-pie>
  </div>
</template>

<script>
  export default {
    data () {
      this.typeArr = ['line', 'histogram', 'pie'];
      this.index = 0;
      return {
        lineChartData: { columns: [], rows: [] },
        itemList: [
          { type: 'primary', name: '周数据', index: 0, dataSource: 'days' },
          { type: '', name: '月数据', index: 1, dataSource: 'week' },
          { type: '', name: '季度数据', index: 2, dataSource: 'month' },
          { type: '', name: '年数据', index: 3, dataSource: 'quarter' },
        ],
      }
    },
    methods: {
      clickButton(item){
        for (let i = 0; i < this.itemList.length; i++) {
          this.$set(this.itemList[i], 'type', '');
        }
        this.$set(this.itemList[item.index], 'type', 'primary');
        this.handleCommand(item.dataSource);
      },
      handleCommand(command) {
        this.lineChartData.columns = [];
        this.lineChartData.rows = [];
        if(command=='days'){
          this.lineChartData.columns.push('date','下单量','销售额');
          for (let index = 0; index < this.changeData.days.length; index++) {
            const element = this.changeData.days[index];
            this.lineChartData.rows.push(element);
          }
        }
        if(command=='week'){
          this.lineChartData.columns.push('date','下单量','销售额');
          for (let index = 0; index < this.changeData.week.length; index++) {
            const element = this.changeData.week[index];
            this.lineChartData.rows.push(element);
          }
        }
        if(command=='month'){
          this.lineChartData.columns.push('date','下单量','销售额');
          for (let index = 0; index < this.changeData.month.length; index++) {
            const element = this.changeData.month[index];
            this.lineChartData.rows.push(element);
          }
        }
        if(command=='quarter'){
          this.lineChartData.columns.push('date','下单量','销售额');
          for (let index = 0; index < this.changeData.quarter.length; index++) {
            const element = this.changeData.quarter[index];
            this.lineChartData.rows.push(element);
          }
        }
      },
      initData(){
        this.postRequest('/v1/merchantsTrend')
        .then(res => {
          if(res.data.code=='200'){
            this.changeData = res.data.data;
            this.lineChartData.columns = [];
            this.lineChartData.rows = [];
            this.lineChartData.columns.push('date','下单量','销售额')
            this.lineChartData.rows = res.data.data.days;
          }
        })
      }
    },
    created(){
      this.initData();
    },
  }
</script>

<style lang="scss" scoped>
.SalesAnalysis {
  height: 88vh;
  text-align: left;
  padding-top: 20px;
  background-color: #fff;
}
.buttonCon {
  margin-bottom: 10px;
}
.el-button--small {
  margin-bottom: 20px !important;
}
.buttonStyle {
  margin-left: 30px;
}
</style>
